@import "https://fonts.googleapis.com/css?family=Raleway:300,400";

.admin-sidenav {
  width: auto;
  height: auto;
  margin-left: 0px;
}
.admin-sidenav a {
  text-decoration: none;
}
.admin-sidenav li {
  text-align: justify;
  padding: 0.5rem;
  padding-left: 1rem;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
  background-color: #000;
  border: 1px solid #333;
}
.admin-sidenav li a {
  color: #fff;
}

.admin-sidenav li a:active {
  border-color: #02d3f5;
}

.admin-sidenav li:hover {
  border-radius: 0 0.5rem 0.5rem 0;
  border-color: #02d3f5;
  -webkit-transform: translate(30px, 0px);
  transform: translate(30px, 0px);
  background: -webkit-linear-gradient(left, #006a7b, #002340);
  background: linear-gradient(to right, #006a7b, #002340);
}

.admin-sidenav li:active {
  border-color: #02d3f5;
}

.admin-sidenav .side-item {
  color: white;
}
